import React from 'react';
import './Button.css';

const isEqual = val => {
    return (val === "=");
}

const changeColor = val => {
    if(val === "=") {
        return {backgroundColor: "cornflowerblue"}
    } else if(val === "+" || val === "-" || val === "*" || val === "/") {
        return {backgroundColor: "dimgrey"}
    }
}

export const Button = props => (
    <div style={changeColor(props.children)} className={`button ${isEqual(props.children) ? "double" : null}`} onClick={() => props.handleClick(props.children)}>
        {props.children}
    </div>
);
