import React, { Component } from 'react';
import './App.css';
import Header from './Comp/Header/Header';
import Footer from './Comp/Footer/Footer';
import Body from './Comp/Body/Body';

class App extends Component {

    constructor() {
        super();

        this.state = {
            show: "Calculator"
        }
    }

    handleClick = val => {
        this.setState({ show: val});
    }

    render() {
        return (
            <div className="app">
                <Header show={this.state.show}/>
                <Body show={this.state.show}/>
                <Footer onClick={this.handleClick}/>
            </div>
        );
    }

}

export default App;
