import React from 'react';



class FooterLink extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            displayed: this.props.val
        }

    }

    render() {
        return (
            <div className="link" onClick={() => this.props.onClick(this.state.displayed)}>
                {this.props.val}
            </div>
        )
    }
}

export default FooterLink;
