import React from 'react';
import Calculator from './Calculator';
import Gpa from './Gpa';
import Grades from './Grades';

class Body extends React.Component {

    constructor(props) {
        super(props);
    }

    render() {

        if(this.props.show === "GPA Calculator") {
            return (
                <Gpa/>
            );
        } else if(this.props.show === "Class Grade Calculator") {
            return (
                <Grades/>
            );
        } else if(this.props.show === "Calculator") {
            return (
                <div class="calculator">
                    <Calculator styles = {this.calcStyles} show={this.props.show}/>
                </div>
            );
        }
    }

}

export default Body;
