import React from 'react';
import Assignment from './Assignment';

class Grades extends React.Component {

    constructor() {
        super();

        this.state = {
            assignments: [1],
            totalGrade: 0,
        }

        this.myRef = {}
    }

    handleClick = () => {
        const next = this.state.assignments.length+1;

        this.setState({ assignments: this.state.assignments.concat(next) });
    }

    calcGrade = () => {
        let total = 0;

        for(let i=0;i<this.state.assignments.length;i++) {
            let newAmount = this.myRef[i+1].state.grade*this.myRef[i+1].state.percentage/100;
            total = total + newAmount;
        }

        this.setState({ totalGrade: total });
    }

    render() {
        return (
            <div className="classWrapper">
                {
                    this.state.assignments.map((assignmentId) => (
                        <Assignment id={assignmentId} key={assignmentId} ref={(ref) => this.myRef[assignmentId] = ref}/>
                    ))
                }
                <button className="add" onClick={this.handleClick}>Add Another</button>
                <div className="grade">
                    Class Grade: {this.state.totalGrade}%
                    <button className="compute" onClick={this.calcGrade}>Compute</button>
                </div>
            </div>
        )
    }
}

export default Grades;
