import React from 'react';

class Assignment extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            grade: 0,
            percentage: 0
        }
    }

    changeGrade = (event) => {
        this.setState({ grade: event.target.value });
    }

    changePercentage = (event) => {
        this.setState({ percentage: event.target.value });
    }

    render() {
        return (
            <div className="assignment">
                <div className="name">
                    <p>Assignment</p>
                    <input type="text"/>
                </div>
                <div className="grade">
                    <p>Grade</p>
                    <input type="number" min="0" max="100" onChange={this.changeGrade}/>
                </div>
                <div className="weight">
                    <p>Percentage of Total</p>
                    <input type="number" min="0" max="100" onChange={this.changePercentage}/>%
                </div>
            </div>
        )
    }
}

export default Assignment;
