import React from 'react';

class Class extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            gradePoints: 0,
            grade: 0,
            credits: 0
        }
    }

    change = (event) => {
        if(event.target.nodeName === "SELECT") {
            this.setState({ grade: event.target.value }, function() {
                this.setState({ gradePoints: (this.state.grade * this.state.credits).toPrecision(3) });
            });
        } else if(event.target.nodeName === "INPUT") {
            this.setState({ credits: event.target.value }, function() {
                this.setState({ gradePoints: (this.state.grade * this.state.credits).toPrecision(3) });
            });
        }

    }

    render() {
        return (
            <div className={'class ' + this.props.id}>
                <div className="name">
                    <p>Class Name</p>
                    <input type="text"/>
                </div>
                <div className="grade">
                    <p>Grade</p>
                    <select onChange={this.change}>
                        <option value="" default></option>
                        <option value="4">A</option>
                        <option value="3.66">A-</option>
                        <option value="3.33">B+</option>
                        <option value="3">B</option>
                        <option value="2.66">B-</option>
                        <option value="2.33">C+</option>
                        <option value="2">C</option>
                        <option value="1.66">C-</option>
                        <option value="1.33">D+</option>
                        <option value="1">D</option>
                    </select>
                </div>
                <div className="credits">
                    <p>Credits</p>
                    <input type="number" min="0" onChange={this.change}/>
                </div>
                <div className="gradePoints">
                    <p>Grade Points</p>
                    <div>{this.state.gradePoints}</div>
                </div>
            </div>
        )
    }
}

export default Class;
