import React from 'react';
import Class from './Class';

class Gpa extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            classes: [1],
            gpa: 0
        }

        this.myRef = {}
    }

    handleClick = () => {
        const nextId = this.state.classes.length+1;
        this.setState({
            classes: this.state.classes.concat([nextId])
        });
    }

    changeGpa = () => {

        let totalGradePoints = 0;
        let totalCredits = 0;

        for(let i=0;i<this.state.classes.length;i++) {
            totalGradePoints = totalGradePoints + parseFloat(this.myRef[i+1].state.gradePoints);
            totalCredits = totalCredits + parseFloat(this.myRef[i+1].state.credits);
        }

        this.setState({ gpa: (totalGradePoints/totalCredits).toPrecision(3) });
    }

    render() {
        return (
                <div>
                    <div className="classWrapper">
                        {
                            this.state.classes.map((classId) => (
                                <Class id={classId} ref={(ref) => this.myRef[classId] = ref}  key={classId}/>
                            ))
                        }
                        <button className="add" onClick={this.handleClick}>Add Another</button>
                        <div className="gpa">
                            Cumulative GPA: {this.state.gpa}
                            <button className="compute" onClick={this.changeGpa}>Compute</button>
                        </div>

                    </div>
                </div>

        )
    }
}

export default Gpa;
